interface UserData {
  name: string;
  email: string;
  password: string;
}

type CreateUserResult = { message?: string[]; user?: UserData };

export const createUserAction = (
  prevFormData: CreateUserResult,
  formData: FormData
): CreateUserResult => {
  const user = {
    name: formData.get("name") as string,
    email: formData.get("email") as string,
    password: formData.get("password") as string,
  };

  console.log(formData);
  console.log(user);

  const errors: string[] = [];

  if (user.name.trim() === "") {
    errors.push("Please fill the name");
  }
  if (user.password.trim().length < 5) {
    errors.push("Password must be 6 charachter");
  }

  if (errors.length > 0) {
    return {
      user: user,
      message: errors,
    };
  }

  console.log(user, errors);

  return {
    message: [],
  };
};
